  'Те-пе-пе-пе… чам-чам-чам-чам… (передразнивает) Сучонок, ты посмотри, сколько времени, негодяй. Я тебе всё равно приговор на этой неделе вынесу, я тебе сказал...',
  'Все ваши...',
  'Вся... вся ваша кодла — вся под колпаком. Вся ваша вонючая, причём мелко... такая, знаешь, ничтожная, сволочная... Тут сегодня приезжали два на своих велосипедах, я сказал твоей мальвине, а увезли их на персоналке. Блядь, обоих на карете скорой помощи. В морг обоих, блядь.',
  'И тебя. Следующий ты, следующий ты кандидат вместе с мальвинами, с подсадными утками, блядь, все, все же вы в паутине здесь.',
  'Я тебе ещё раз сказал, я тебе такого мальчика, ты салажонок, я тебе такого мальчика, что ты больше с Атлантики никогда не выйдешь, сука, на корм рыбам сразу пойдёшь, дешёвец поганый.',
  'Тебе ответили, я тебе... ишь ты зараза подлючая, ползун поганый! Тварюга поганая, всех вас передавлю, как клопов, так сказал Каттани, сука, запомни...',
  'Ебучая тварь! Ты куда... Я тебе покажу, я тебе покажу по телевизору скоро, вот на этой неделе, тварюга ты тупоносая.',
  'Тупоносая, я тебе... Да это ты полные штаны со своей мальвиной насрал. Вас же приволокут и бросят, сук, а я уже с вами найду, как с вами разобраться.',
  'Я тебе, гадёныш поганый!.. Я тебе дам, 3К-4К! Я тебе сказал, я тебя сам залупцую до смерти, суку! Будешь, как, сука, как змеюга на сковородке крутиться, падлюка! Я тебе смирительную рубашку делать не буду, не боишься, ты... Ты, сука, знаешь с кем говоришь, тварюга поганая? Hе боишься, я таких мудаков, как ты, давил собственными руками, как клопов, падла. А тебе, я тебе сказал, тебе, значит, таким образом: баржа, сука, самая тихоходная, и в Атлантику на корм рыбам, падлюка ебучая.',
  'Кедовская тварь подлючая. И за всё, что ты натворил, что ты сделал, я тебе устрою красивую жизнь, сучонок. Вместе с, вместе с твоей мальвиной из ж... из жопы дно выбью, выбью, до конца. Обоим, сукам. И ещё твоему, этому, прихлебателю, блядь, там ещё, понимаешь это, на спаренном эт... на спаренном телефоне. Сучонок ты подлючий.',
  'Помой ноги вонючие и харю свою тупую протри. Посмотри в зеркало, ебучая ты тварюга, блядь, жертва неудачного аборта.',
  'Ааа, ты мне, блядь, шарманку низкопробную, блядь, подсовывает, фарцовщик ебаный.',
  'Я тебе сказал, мудила, фарцовщик ты ебучий низкопробный, д-дрань ты поганая, рванина, блядь, нищета ты нищебродная.',
  'Ты понял? Вот этой вот шарманкой я тебе размозжу там башку прямо, сука, в прокуратуре. Прямо этой шарманкой, этим черным ящиком, нд, блядь, твоим дешёвым, блядь. Хороша, да... видишь, какое дело — ты сам себя же опровергаешь, сучонок ты ебучий, что ты занимаешься не тем, чем нужно…',
  '…Ты насрал полные штаны, сучонок, насрал вместе со своими, блядь, оболтусами, и теперь ты думаешь отм... отмыться, но этого не получится, дурень ты чёртовый, блядь, говноед ты ебучий, подавись ты, сучонок... говном, подлюка, чтоб ты изошёл весь, сука, и ссал кровью, подлюка ебучая.',
  'Да, я вас слушаю.',
  'Бухало! Ты все бу́хаешь, блядь? Заткни ебало, зараза ебучая.',
  'Я те... я тебе согласен, я тебе голову оторву, я на это, конечно, согласен, подлюка ты ебучая.',
  'И вместо... ты знаешь что... ты у нас в парке там ходил, ты лучше этому, японцу отдай магнитофон, что б я тебе ебало не раскроил им сразу, потому что ему там для диктофона он пригодится.',
  'Да, и где ты ба-базируешься, я уже засёк, всё, понимаешь ли, всё. Ну а теперь жди, сука, возмездия, зараза ебаная.',
  'Бухало! Бухало, заткни свое вонючее ебало, я тебе сказал, сучонок.',
  'Группа захвата слушает вас.',
  'Ты, зараза про... поганая, ты уже приехал?',
  'Щас я тебя повяжу, сучонок.',
  'Ты, дурачок! Ты ещё не совсем вывихнулся?',
  'Ну сегодня, щас тебе поздравление будет великолепное! В ближайшее, намедни! Такое тебе поздравление будет, что ты дара речи лишишься сразу, дурачок.',
  'Ну вот сегодня, так у те... у те... у тебя всё время праздник, потому что ты вывихнутый по всем осям и параметрам. Тебя же стабилизировать не надо ни по курсу, ни по тангажу, ни по рысканью. Ты и так мордой колупаешься тупой.',
  'Пошёл отсюда вон, зараза ебучая.',
  'Ты, пожалуйста, прекрати эти звонки, потому что сегодня буду вышибать жопу тебе и твоему другу.',
